<template>
    <div class="favorites-view" v-if="Object.keys(pageContent).length">
        <page-title :title="pageContent.pageTitle[lang]"/>
        <div class="wrapper">
            <div class="favorites-list" v-if="productsInfo.length">
                <div class="flex-row-12">
                    <div class="flex-col xs-12 sm-6 md-4" v-for="(product, i) in productsInfo" :key="i">
                        <product-card :product="product" :isFavoritesCard="true"/>
                    </div>
                </div>
            </div>
            <div class="msg" v-else>
                <span class="fas fa-sad-tear"></span>
                <p>{{ pageText.emptyMsg[lang] }}</p>
                <router-link class="btn" :to="{ name: 'categories'}">
                    <span class="btn-icon fas fa-list"></span>
                    <span class="btn-text">{{ pageText.catalogMoreBtn[lang] }}</span>
                </router-link>  
            </div>
        </div>
        <questions-form />
    </div>
</template>

<script>
import QuestionsForm from '../components/QuestionsForm.vue';
import PageTitle from '../components/PageTitle.vue';
import ProductCard from '../components/ProductCard.vue';

export default {
    data() {
        return {
            pageText: {
                emptyMsg: {
                    ua: 'Нажаль, тут порожньо',
                    ru: 'К сожалению, тут пусто',
                    en: 'Sorry, but favorites is empty'
                },
                catalogMoreBtn: {
                    ua: 'Перейти до каталогу',
                    ru: 'Перейти в каталог',
                    en: 'To catalog'
                }
            }
        }
    },
    computed: {
        lang() {
            return this.$store.getters['getSelectedLang'];
        },
        pageContent() {
            return this.$store.getters['getFavoritesPageContent'];
        },
        products() {
            return this.$store.getters['getProductsInFavorites'];
        },
        productsInfo() {
            let products = [];
            this.products.forEach(el => {
                products.push(this.$store.getters['getProductById'](el));
            });
            return products;
        }
    },
    components: {
        QuestionsForm,
        ProductCard,
        PageTitle
    }
}
</script>