<template>
    <div class="product-card">
        <button class="favorites" v-if="isFavoritesCard" @click="removeFromFavorites()">
            <span class="fas fa-times"></span>
        </button>
        <div class="image">
            <img :src="product.image">
        </div>
        <div class="card-title">{{ product.name[lang] }}</div>
        <div class="card-actions">
            <router-link :to="{ name: 'product', params: { categoryId: product.category, productId: product.code } }"  class="more">
                <span class="text">{{ buttonText[lang] }}</span>
                <span class="fas fa-arrow-right"></span>
            </router-link>
        </div>
        <p class="card-price">{{ product.cost }} UAH</p>
        
    </div>
</template>

<script>
    export default {
        data() {
            return {
                buttonText: {
                    ua: 'Перейти',
                    ru: 'Перейти',
                    en: 'See more'
                }
            }
        },
        props: {
            product: Object,
            index: Number,
            isFavoritesCard: Boolean
        },
        computed: {
            lang() {
                return this.$store.getters['getSelectedLang'];
            }
        },
        methods: {
            removeFromFavorites() {
                this.$store.dispatch('removeProductFromFavorites', this.index);
            }
        }
    }
</script>
